import React from "react";
import { Redirect } from "react-router-dom";

class Login extends React.Component {
    state = {
        email: '',
        password: '',
        submitRedirectFb: false,
        invalidLogin: false
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    handleSubmit = () => {
        let {email, password, invalidLogin} = this.state;
        if (email.length > 2 && password.length > 2) {
            let obj = { 'email': email, 'password': password }
            fetch(`https://multer-test123.herokuapp.com/signin`, {
                method: 'POST',
                body: JSON.stringify(obj),
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                if (res.status === 200) {
                    return res.json()
                } else {
                    this.setState({invalidLogin: true})
                    return res.json()
                }
            })
            .then(data => {
                console.log(data)
                if (invalidLogin === false) {
                    //set cookie
                    // this.setState({submitRedirectFb: true})
                } else {
                    //set error message
                }
            })
        }
    }
    render() {
        const {email, password, submitRedirectFb, invalidLogin} = this.state;
        return (
            <React.Fragment>
                {submitRedirectFb === true && invalidLogin === false ? <Redirect to="/fb" /> : null}
                <h1>Sign In</h1>
                <span className="alert"></span>
                <label>Email:</label>
                <input 
                id="email"
                value={email} 
                onChange={(e) => this.handleChange(e)}
                className="input" />
                <label>Password:</label>
                <input 
                id="password"
                value={password} 
                onChange={(e) => this.handleChange(e)}
                className="input" />
                <div className="btnContain">
                    <button 
                    onClick={this.handleSubmit}
                    className="btn login"
                    >
                        <div className="lds-defaultTwo">
                        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
                        </div>
                        <span className="loginTxt">LOGIN</span>
                    </button>
                    <button className="btn signup">
                        <span className="signTxt"> SIGNUP </span>
                    </button>
                </div>
            </React.Fragment>
        )
    }
}

export default Login;